font-weight:300;transition:.5s;letter-spacing:1px;border-radius:8px}.button-container-2 button{width:101%;height:100%;font-family:"Lato",sans-serif;font-weight:300;font-size:11px;letter-spacing:1px;font-weight:bold;background:#000;-webkit-mask:url("https://raw.githubusercontent.com/robin-dela/css-mask-animation/master/img/urban-sprite.png");mask:url("https://raw.githubusercontent.com/robin-dela/css-mask-animation/master/img/urban-sprite.png");-webkit-mask-size:3000% 100%;mask-size:3000% 100%;border:none;color:#fff;cursor:pointer;-webkit-animation:ani2 .7s steps(29) forwards;animation:ani2 .7s steps(29) forwards}.button-container-2 button:hover{-webkit-animation:ani .7s steps(29) forwards;animation:ani .7s steps(29) forwards}.mas{position:absolute;color:#000;text-align:center;width:101%;font-family:"Lato",sans-serif;font-weight:300;position:absolute;font-size:11px;margin-top:17px;overflow:hidden;font-weight:bold}@-webkit-keyframes ani{from{-webkit-mask-position:0 0;mask-position:0 0}to{-webkit-mask-position:100% 0;mask-position:100% 0}}@keyframes ani{from{-webkit-mask-position:0 0;mask-position:0 0}to{-webkit-mask-position:100% 0;mask-position:100% 0}}@-webkit-keyframes ani2{from{-webkit-mask-position:100% 0;mask-position:100% 0}to{-webkit-mask-position:0 0;mask-position:0 0}}@keyframes ani2{from{-webkit-mask-position:100% 0;mask-position:100% 0}to{-webkit-mask-position:0 0;mask-position:0 0}}a{color:#00ff95}.button-container-3{position:relative;width:200px;height:50px;margin-left:auto;margin-right:auto;margin-top:8vh;overflow:hidden;border:1px solid #253d58;font-family:"Inter",sans-serif;font-weight:300;transition:.5s;letter-spacing:1px;border-radius:8px;box-shadow:3px 10px 20px 0px #000}.button-container-3 button{width:101%;height:100%;font-family:"Inter",sans-serif;font-weight:300;font-size:18px;letter-spacing:1px;font-weight:bold;box-shadow:3px 1px 10px 3px #000;background:#253d58;-webkit-mask:url("https://raw.githubusercontent.com/pizza3/asset/master/natureSmall